	module.exports = {"data":{"markdownRemark":{"headings":[],"html":"<p>Virtual Gestures is a series of interactive objects examining reverse skeumorphism. It explores how immaterial objects look when given the sense of materiality and feel when digital gestures of scrolling, dragging, resizing and clicking are performed physically. By bringing the digital to the physical world, the digital is radically transformed into bizarre behaviours and nonsensical objects, revealing the gap between how we experience these two distinct yet intertwined worlds. </p>\n<p><a href=\"soojinna.com/virtualgestures\">Click here</a> to learn more about the project.</p>","frontmatter":{"title":"Virtual Gestures","graduate":"Soojin Kim","category":"Experiential Design","category2":"Installation","category3":"Web Design","image":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.5,"src":"/static/virtual-gestures-be3f415f5f051f50b8b1e592632698e0-a7a62.jpg","srcSet":"/static/virtual-gestures-be3f415f5f051f50b8b1e592632698e0-5a1a9.jpg 375w,\n/static/virtual-gestures-be3f415f5f051f50b8b1e592632698e0-0908c.jpg 750w,\n/static/virtual-gestures-be3f415f5f051f50b8b1e592632698e0-a7a62.jpg 1500w,\n/static/virtual-gestures-be3f415f5f051f50b8b1e592632698e0-9f250.jpg 1800w","sizes":"(max-width: 1500px) 100vw, 1500px"}}}}}},"pathContext":{"slug":"/work/virtual-gestures/","name":"/Virtual Gestures/"}}

